
import react, { useEffect, useState } from 'react';
import App from '../../App.css';
import emailjs  from 'emailjs-com';
import { init } from 'emailjs-com';
import ContactCss from './ContactCss.css';
import {db} from '../firebase';



export default function Contact(){
    const[name,setName] = useState("");
    const[phoneno,setPhoneno] = useState("");
    const[message,setMessage] = useState("");
    const[submit,setSubmit] = useState(false);



    const[info,setInfo] = useState({
        name: "",
        phoneno:"",
        message:"",
    });
    const state= (event)=>{
        console.log("submit called...");

        db.collection("contacts").add({
            name:name,
            phoneno:phoneno,
            message:message,
        })
        .then(()=>{
            alert("submitted");
        })

        setInfo({
            ...info,
            name:event.target.name,
            phoneno:event.target.phoneno,
            message:event.target.message,
            
        })
       
       
        setSubmit(true);


    }

    return(
       <>
       {submit ?
       <div className="confirmation-container"> 
       <h2 className="confirmation-page">Thanks for the order. 
                <br/>We will contact you soon..
                 </h2>
                </div>
       :(
       <section>
           
           <div className="container">  
            <form onSubmit ={state}>
              <div class="form-group">
                  <label for="name"> Name: </label>
                  <input type="text" id="name"  name="name" onChange={(e)=>setName(e.target.value)}/>
              </div>
             
              <div class="form-group">
                  <label for="phoneno">Mobile No: </label>
                  <input type="text" id="phoneno"  name="phoneno" onChange={(e)=>setPhoneno(e.target.value)}/>
              </div>
              <div class="form-group">
                  <label for="messgae">Order Details: </label>
                  <textarea  id="message" name="message" onChange={(e)=>setMessage(e.target.value)} cols="20" rows="10"/>
              </div>
              <input type="submit" value="Submit" className ='form-group-button'/>
              
            </form>
           </div>
       </section>
       )}
       </>
    )
}